const axiosClient = axios.create({
  baseURL: variable.jsonplaceholerAPI,
  headers: {
    "content-type": "application/json",
  },
  //   ! Vic parse param ca asiox c hi
  // !vn  nn ta s s dng query string  x l vic ny

  //? V d nh ta phi vit query stirng th ny : ?userid=""&password="". Kh l mt,
  //?dng ci th vi ny ta ch cn b param vo thi, cn li n s t chuyn thnh query trn
  paramsSerializer: (params) => queryString.stringify(params),
});
axiosClient.interceptors.request.use(async (config) => {
  return config;
});

// * mi reponse ta s phi thc hin ly
axiosClient.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    throw error;
  }
);

export const axiosTeamClient = axios.create({
  baseURL: variable.teamBaseURL,
  headers: {
    "Content-type": "application/json",
    Authorization: `Bearer ${localStorage.getItem("token")}`,
    "Access-Control-Allow-Headers": "*",
  },
  paramsSerializer: (params) => queryString.stringify(params),
});

axiosTeamClient.interceptors.request.use(
  async (config) => {
    return config;
  },
  function (error) {
    return Promise.reject(error);
  }
);

axiosTeamClient.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    throw error;
  }
);

/**
 * * Axios dnh ring cho nhng api ko cn token
 */
export const axiosWithoutToken = axios.create({
  baseURL: variable.teamBaseURL,
  headers: {
    "Content-type": "application/json",
    "Access-Control-Allow-Origin": "*",
  },
  paramsSerializer: (params) => queryString.stringify(params),
});
// ! X l phn authen token, mi api  c ci ny mi
axiosWithoutToken.interceptors.request.use(async (config) => {
  config.headers = {
    "Access-Control-Allow-Origin": "*",
  };
  return config;
});
axiosWithoutToken.interceptors.response.use(
  (response) => {
    response.headers = {
      "Access-Control-Allow-Origin": "*",
    };
    return response;
  },
  (error) => {
    throw error;
  }
);

export default axiosClient;
