import JSONPLACEHOLDERApi from "api/JSONPLACEHOLDERApi";
import * as variable from "../../variables/Variables";
const { createSlice, createAsyncThunk } = require("@reduxjs/toolkit");

export const getDashboard = createAsyncThunk(
  "dashboard/getDashboard",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getDashboard(null);
    return result;
  }
);
export const getTop10 = createAsyncThunk(
  "dashboard/getTop10",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getTop10(params);
    return result;
  }
);
export const getShareAndExchangeCompare = createAsyncThunk(
  "dashboard/getShareAndExchangeCompare",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getShareAndExchangeCompare(params);
    return result;
  }
);

/**
 * * C mt cht khc bit khi ta to async action,
 * * thay v hi trc slice m ta to ra s cho ta 2 bin l reducer v actions th
 * * by h ta ko  actions tn ngn vy na m s export ra thng ci async action no cn lunlun
 */

/**
 * * tham s u s l param m ta mun pass vo ci hm ny
 * * ci tip theo l mt th m hm 'createAsyncThunk' cung cp cho php ta c dispatch nhng
 * * action  cc ni khc  dng trong hm ny: thunkAPI.dispatch()
 */

const dashboardSlice = createSlice({
  name: "dashboard",
  initialState: {
    loading: false, // * trng ny dng  hin th pop up loading trong lc di API tr v response
    success: false, // * trng ny dng  hin th pop up success nu nh gi API thnh cng
    fail: false, //* trng ny dng  hin th pop up fail nu nh gi API tht bi
    result: [],
    error: "",
    updateDashboard: [
      {
        row: {
          cols: [
            {
              elementType: variable.input,
              elementConfig: {
                name: "updateTime",
                type: variable.select,
                labeltext: "Thi gian xem bo co",
                value: "",
              },
              validation: [],
              valid: {},
            },
          ],
        },
      },
    ],
  },
  reducers: {},

  // ! Vi kt qu tr v u s c 3 trng l meta,data v error, ta vit  di ny  coi s phi lm nhng g cho tng loi kt qu
  extraReducers: {
    [getDashboard.fulfilled]: (state, action) => {
      state.result = action.payload;
    },
    [getDashboard.rejected]: (state) => {
      console.log("Dashboard ly v ko thnh cng");
    },
    [getDashboard.pending]: (state) => {
      console.log("Dashboard ang pending");
    },
  },
});

const { reducer: dashboardReducer, actions } = dashboardSlice; //createSlice s tr v cho ta 2 bin l reducer v action
export const {} = actions;
export default dashboardReducer;
