import JSONPLACEHOLDERApi from "api/JSONPLACEHOLDERApi";
import * as variable from "../../variables/Variables";
const { createSlice, createAsyncThunk } = require("@reduxjs/toolkit");

/**
 * * C mt cht khc bit khi ta to async action,
 * * thay v hi trc slice m ta to ra s cho ta 2 bin l reducer v actions th
 * * by h ta ko  actions tn ngn vy na m s export ra thng ci async action no cn lunlun
 */

/**
 * * tham s u s l param m ta mun pass vo ci hm ny
 * * ci tip theo l mt th m hm 'createAsyncThunk' cung cp cho php ta c dispatch nhng
 * * action  cc ni khc  dng trong hm ny: thunkAPI.dispatch()
 */

//* Hm dng  cp nht ngng bo co
export const updatePercentThreshold = createAsyncThunk(
  "PrecentReport/put",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.editPrecentReport(params);
    return result;
  }
);

//* Hm dng  load cc ngng bo co
export const getAllPercentReport = createAsyncThunk(
  "PrecentReport/GetAll",
  async (params, thunkAPI) => {
    const result = await JSONPLACEHOLDERApi.getAllPercent(null);
    return result;
  }
);

const systemConfig = createSlice({
  name: "systemConfig",
  initialState: {
    percentResult: [
      {
        id: 1,
        name: "Phn trm s lng account",
        precent: 40,
      },
      {
        id: 2,
        name: "Phn trm s lng bi Post",
        precent: 30,
      },
    ],
    loading: false, // * trng ny dng  hin th pop up loading trong lc di API tr v response
    success: false, // * trng ny dng  hin th pop up success nu nh gi API thnh cng
    fail: false, //* trng ny dng  hin th pop up fail nu nh gi API tht bi
    error: "",
    selectedId: "",
    percent: "",
    percentNames: [],
    systemConfigForm: [
      {
        row: {
          cols: [
            {
              elementType: variable.select, // Loi input
              elementConfig: {
                name: variable.percentName,
                type: variable.select,
                labeltext: "Loi ngng bo co",
                options: [],
                value: "",
              },
            },
          ],
        },
      },
    ],
  },
  reducers: {
    //   ! Hm dng  a cc la chn v loi percent vo form
    addPercentReportName: (state, action) => {
      //* Ci phn commend ny l dng  ly t API
      state.systemConfigForm[0].row.cols[0].elementConfig.options =action.payload;
      let arra = [];
      state.systemConfigForm[0].row.cols[0].elementConfig.options.map(
        (percent) => {
          arra.push({ key: percent.name, value: percent.id });
        }
      );
      state.percentNames = arra;
    },

    //* Hm ny dng  ly ra ci percent ng vi ci ID
    getPercentById: (state, action) => {
      state.systemConfigForm[0].row.cols[0].elementConfig.options.map((item) => {
        if (action.payload == item.id) {
          state.percent = item.precent;
        }
      });
    },
  },

  // ! Vi kt qu tr v u s c 3 trng l meta,data v error, ta vit  di ny  coi s phi lm nhng g cho tng loi kt qu
  extraReducers: {
    [getAllPercentReport.pending]: (state) => {
      state.loading = true;
    },
    [getAllPercentReport.rejected]: (state, action) => {
      state.error = action.error;
      state.loading = false;
    },
    [getAllPercentReport.fulfilled]: (state, action) => {
      state.percentResult = action.payload;
      state.loading = false;
    },
    [updatePercentThreshold.pending]: (state) => {
      state.loading = true;
    },
    [updatePercentThreshold.rejected]: (state, action) => {
      state.error = action.error;
      state.loading = false;
    },
    [updatePercentThreshold.fulfilled]: (state, action) => {
      state.percentResult = action.payload;
      state.loading = false;
    },
  },
});

const { reducer: systemConfigReducer, actions } = systemConfig; //createSlice s tr v cho ta 2 bin l reducer v action
export const { addPercentReportName, getPercentById } = actions;
export default systemConfigReducer;
